import React, { Component } from 'react';
import Moment from 'react-moment';
import 'moment-timezone';

class Device extends Component {
  constructor(props){
    super(props);
    this.state = {
      device: props.device
    }
  }
  componetDidRecieveProps(nextProps){

  }

  render() {
    const { device } = this.state;
    const date = device.date
    // console.log(date)
    return (
    <div className="device_details">
      <div className={device.class_name}></div>
      <div className="main">
        <div className="first">{device.class_name}</div>
        <div className="child">{device.start_time_reformat} - {device.end_time_reformat}</div>
      </div>
      <div className="main">
        <div className="second"> Clients : {device.client_count}</div>
        <div className="child"><Moment format="D MMM YYYY">{date}</Moment></div>
      </div>
      <div className="main">
        <div className="third">{device.subject_name}</div>
      </div>
      <div className="main">
        <div className="third">{device.first_name} {device.middle_name} {device.last_name}</div>
      </div>
      <div className="bordertop">
        <div className="fourth">Sent : {parseFloat(device.sent_data/1024).toFixed(2)} MB </div>
        <div className="fourth"> Recv : {parseFloat(device.recv_data/1024).toFixed(2)} MB</div>
      </div>
    </div>
    );
  }
}

export default Device;



