import React, { Component } from 'react';
import logo from './logo.png';
import Device from './components/device';
import 'moment-timezone';
import './App.css';
import './components/style/saurav.css'

class App extends Component {

  constructor(props){
    super(props);
    this.state= {
      dev: [],
      sorted: [],
      obj: []
    }
  }

  componentDidMount(){
    console.log('component has mounted')
    this.pulldata();
    this.getdate();
  }


  // componentDidUpdate(){
  //     console.log('update')
  //     this.pulldata();
  // }

  getdate(){
    fetch('http://localhost:3000/api/date')
    .then(function(response){
      response.json()
      .then(function(data){
        console.log(data);
      })
    })

  }

  getSchoolName(id){
    switch (id) {
      case 'L_698057942242429306':
      return 'Al Suqoor';
      case 'L_698057942242429307':
      return 'Fatima Bint Mubarak';
      case 'L_698057942242429308':
      return 'Saad Bin Moaath';
      case 'L_698057942242429362':
      return 'Al Dhaher';
      case 'L_698057942242429379':
      return 'Atika Bint Abdel Muttalib ';
      case 'L_698057942242429390':
      return 'Al Khair';
      case 'L_698057942242429404':
      return 'Qatr Al Nada';
      case 'L_698057942242429426':
      return 'Al Nukhba';
      case 'L_698057942242429717':
      return 'Makkah School';
      default:
      return 'some school';
    }
  }

  async pulldata(){
    var that = this;
    await fetch('http://localhost:3000/api/fetchdata')
    .then(function(response){
      response.json()
      .then(function(data){
        that.setState({
          dev: data
        })
        that.sortdata();
      })
    })
  }

  // async test(){
  //   var that = this
  //   // Get me the data here
  //   await fetch('http://localhost:3000/api/showview')
  //   .then(function(response){
  //     response.json()
  //     .then(function(data){
  //       // console.log('-------');
  //       // Testing
  //       var obj = {
  //         'L_698057942242429306': [],
  //         'L_698057942242429307': [],
  //         'L_698057942242429379': []
  //       };
  //       for(var i in data){
  //         obj[data[i].school_id].push(data[i]);
  //       }
  //       console.log(obj['L_698057942242429306']);
  //       that.setState({
  //         obj: obj
  //       })
  //     })
  //   })
  // }

  sortdata(){
    const { dev } = this.state;
    var that = this;
    // const { data } = dev;
    const schoolIds = [
      "L_698057942242429306",
      "L_698057942242429307",
      // "L_698057942242429308",
      "L_698057942242429362",
      "L_698057942242429379"
      // "L_698057942242429390",
      // "L_698057942242429404",
      // "L_698057942242429426",
      // "L_698057942242429717"
    ];
    const dataObject = schoolIds.map(id => {
      const devices = dev.filter(device => {
        if(device.school_id === id)
        return {
          name: device.s_name,
          classname: device.class_name,
          serial: device.serial,
        };
      }
    )
    return {
      id,
      name: that.getSchoolName(id),
      devices,
    };
  });
  that.setState({
    sorted: dataObject
  })
  // console.log(dataObject);
}


render() {
  const { sorted } = this.state;
  console.log(sorted);
  return (
    <div className="App">
      <header className="App-header">
        {/* <img src={logo} className="App-logo" alt="logo" /> */}
        <div className="App-title">ALEF EDUCATION <div className="big">|</div> NETWORK MONITORING SYSTEM</div>
      </header>
      <div className="container">
        {
          sorted.map((school, schoolKey) => {
            return (
              <div key={schoolKey} className="bigcontainer">
                <div className="title">{school.name}</div>
              <div className="m">
                {
                  school.devices && school.devices.map((device, key) => (
                    <Device device={device} key={key}/>
                  ))
                }
              </div>
            </div>
          )
        })
      }
    </div>
    <div className="footer"><strong>ALEF EDUCATION</strong></div>
  </div>
);
}
}

export default App;



